import {
  React,
  createNumberMask,
  Component,
  showModal,
  connect,
  HiidenFiled,
  ReanderField,
  ReanderCheckBox,
  Field,
  formValueSelector,
  reduxForm,
  ToastNotification,
  getData,
  Camera,
  imgfound,
  convertBase64,
  Gramasi,
  NumberOnly,
  ReanderSelect,
} from "../../../components/helpers/library";

import { getDataMarketplace, getDataSales } from "../../../actions/datamaster_action";
import ValidasiFormPenjualanDataBarang from "../../../Validasi/Penjualan/ValidasiFormPenjualanDataBarang";
import { setCamera } from "../../../actions/databarang_action";
import Loading from 'react-fullscreen-loading';
import { getImage, postData2 } from "../../../config/axios";
import { getUserdata, pembulatan } from "../../../components/helpers/function";
import { getParaSystemId } from "../../../actions/utility_action";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});

class ModalTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ModalWebCam: false,
      isLoading: false,
      harga_jual: true,
      berat_jual: true,
      hargapergram: true,
      otorisasi: false,
      ongkos: true,
      total: true,
      gambar: false,
    };
  }
  showModalWebcam() {
    this.props.dispatch(setCamera(false));
    this.props.dispatch(showModal());
    this.setState({ ModalWebCam: true });
  }
  handleTakePhoto(url) {
    this.props.dispatch(setCamera(url));
    this.props.change("foto", url);
    this.setState({
      ModalWebCam: false,
    });
  }

  getValue = async (event) => {
    const file = event.target.files[0];
    const base64 = await convertBase64(file);
    this.props.change("foto", base64);
    this.props.dispatch(setCamera(base64));
    this.setState({
      gambar: base64,
    });
  };

  getBarcode(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      this.setState({
        isLoading : true
      })
      let cek =
        JSON.parse(localStorage.getItem("data_barang_penjualan_tmp")) || [];
      let hasil_cek = cek.findIndex((hasil) => hasil.kode_barcode === e);
      document.body.style.cursor = "wait"
      if (hasil_cek === -1) {
        getData("barang/ready/kode-barcode/" + e)
          .then((res) => {
            this.setState({
              isLoading : false
            })
            if (res.data.length === 0) {
              ToastNotification("info", "Kode Barcode Tidak Ada");
              this.props.change("nama_barang", "");
              this.props.change("nama_atribut", "");
              this.props.change("berat_atribut", "");
              this.props.change("harga_atribut", "");
              this.props.change("harga_jual", "");
              this.props.change("harga_jual_tmp", "");
              this.props.change("berat", "");
              this.props.change("harga_pergram", "");
              this.props.change("kadar", "");
              this.props.dispatch(setCamera(false));
              this.props.change("foto", "");
              document.body.style.cursor = "initial"
            } else {
              getImage(e)
                .then((hasil) => {
                  if (hasil.data === "") {
                    this.setState({
                      isLoading : false
                    })
                    document.body.style.cursor = "initial"
                    res.data.forEach((list, index) => {
                      let harga_jual = 0;
                      let harga_pergram = 0;
                      if(getUserdata().kode_toko === "PTR"){
                        harga_jual = (parseInt(list.kadar) / 100) * parseFloat(list.berat) * parseFloat(list.harga_skrg);
                        harga_pergram = list.harga_skrg;
                      }else{
                        harga_pergram = parseInt(list.harga_jual) / parseFloat(list.berat);
                        harga_jual = list.harga_jual
                      }
                      this.props.change("nama_barang", list.nama_barang);
                      this.props.change("nama_atribut", list.nama_atribut);
                      this.props.change("berat_atribut", list.berat_atribut);
                      this.props.change("harga_atribut", list.harga_atribut);
                      this.props.change("harga_jual", harga_jual);
                      this.props.change("harga_jual_asli", harga_jual);
                      this.props.change("harga_jual_tmp", harga_jual);
                      this.props.change("berat", list.berat);
                      this.props.change("harga_skrg", list.harga_skrg);
                      this.props.change("harga_pergram", harga_pergram);
                      this.props.change("berat_tmp", list.berat);
                      this.props.change("kadar", list.kadar);
                      this.props.dispatch(setCamera(false));
                      this.props.change("foto", "");
                      document.getElementById("berat").focus()
                      if(getUserdata().kode_toko === "PTR"){
                        let total = parseFloat(harga_jual || 0) + parseInt(list.ongkos || 0) + parseInt(list.harga_atribut || 0);
                        this.props.change("total", total);
                      }else{
                        let total = parseInt(harga_jual || 0) + parseInt(list.ongkos || 0) + parseInt(list.harga_atribut || 0);
                        this.props.change("total", pembulatan(total));
                      }
                      this.setState({
                        harga_jual: false,
                        berat_jual: false,
                        hargapergram: false,
                        ongkos: false,
                        harga_baru : harga_jual
                      });
                    });
                  } else {
                    this.setState({
                      isLoading : false
                    })
                    document.body.style.cursor = "initial"
                    this.props.dispatch(setCamera(hasil));
                    this.props.change("foto", hasil);
                    res.data.forEach((list, index) => {
                      // hargaskarang = 
                      let harga_jual = 0;
                      let harga_pergram = 0;
                      if(getUserdata().kode_toko === "PTR"){
                        harga_jual = (parseInt(list.kadar) / 100) * parseFloat(list.berat) * parseFloat(list.harga_skrg);
                        harga_pergram = list.harga_skrg;
                      }else{
                        harga_pergram = parseInt(list.harga_jual) / parseFloat(list.berat);
                        harga_jual = list.harga_jual
                      }
                      // console.log(harga_jual);
                      this.props.change("nama_barang", list.nama_barang);
                      this.props.change("nama_atribut", list.nama_atribut);
                      this.props.change("berat_atribut", list.berat_atribut);
                      this.props.change("harga_atribut", list.harga_atribut);
                      this.props.change("harga_jual_tmp", harga_jual);
                      this.props.change("harga_jual", harga_jual);
                      this.props.change("harga_jual_asli", harga_jual);
                      this.props.change("berat", list.berat);
                      this.props.change("harga_pergram", harga_pergram);
                      this.props.change("kadar", list.kadar);
                      this.props.change("harga_skrg", list.harga_skrg);
                      this.props.change("berat_tmp", list.berat);
                      document.getElementById("berat").focus()
                      // console.log(total);
                      if(getUserdata().kode_toko === "PTR"){
                        let total = parseFloat(harga_jual || 0) + parseInt(list.ongkos || 0) + parseInt(list.harga_atribut || 0);
                        this.props.change("total", total);
                      }else{
                        let total = parseInt(harga_jual || 0) + parseInt(list.ongkos || 0) + parseInt(list.harga_atribut || 0);
                        this.props.change("total", pembulatan(total));
                      }

                      this.setState({
                        harga_jual: false,
                        berat_jual: false,
                        hargapergram: false,
                        ongkos: false,
                        harga_baru : harga_jual
                      });
                    });
                  }
                })
                .catch((err) => {
                  this.setState({
                    isLoading : false
                  })
                  document.body.style.cursor = "initial"
                  this.props.change("foto", "");
                  this.props.dispatch(setCamera(false));
                  res.data.forEach((list, index) => {
                    let harga_jual = 0;
                      let harga_pergram = 0;
                      if(getUserdata().kode_toko === "PTR"){
                        harga_jual = (parseInt(list.kadar) / 100) * parseFloat(list.berat) * parseFloat(list.harga_skrg);
                        harga_pergram = list.harga_skrg;
                      }else{
                        harga_pergram = parseInt(list.harga_jual) / parseFloat(list.berat);
                        harga_jual = list.harga_jual
                      }
                    this.props.change("nama_barang", list.nama_barang);
                    this.props.change("nama_atribut", list.nama_atribut);
                    this.props.change("berat_atribut", list.berat_atribut);
                    this.props.change("harga_atribut", list.harga_atribut);
                    this.props.change("harga_jual_tmp", harga_jual);
                    this.props.change("harga_jual", harga_jual);
                    this.props.change("harga_jual_asli", harga_jual);
                    this.props.change("berat", list.berat);
                    this.props.change("harga_pergram", harga_pergram);
                    this.props.change("kadar", list.kadar);
                    this.props.change("harga_skrg", list.harga_skrg);
                    this.props.change("berat_tmp", list.berat);
                    document.getElementById("berat").focus()
                    if(getUserdata().kode_toko === "PTR"){
                      let total = parseFloat(harga_jual || 0) + parseInt(list.ongkos || 0) + parseInt(list.harga_atribut || 0);
                      this.props.change("total", total);
                    }else{
                      let total = parseInt(harga_jual || 0) + parseInt(list.ongkos || 0) + parseInt(list.harga_atribut || 0);
                      this.props.change("total", pembulatan(total));
                    }
                    this.setState({
                      harga_jual: false,
                      berat_jual: false,
                      hargapergram: false,
                      ongkos: false,
                      harga_baru : harga_jual
                    });
                  });
                });
            }
          })
          .catch((err) => {
            this.props.change("nama_barang", "");
            this.props.change("nama_atribut", "");
            this.props.change("berat_atribut", "");
            this.props.change("harga_atribut", "");
            this.props.change("harga_jual", "");
            this.props.change("harga_jual_tmp", "");
            this.props.change("berat", "");
            this.props.change("harga_pergram", "");
            this.props.change("kadar", "");
            this.props.change("berat_tmp", "");
            this.props.dispatch(setCamera(false));
            this.props.change("foto", "");
            this.setState({
              isLoading : false
            })
            // console.log();
            document.body.style.cursor = "initial"
            if (err.response?.status !== 404) {
              ToastNotification("info", err.response?.data || "Terjadi Keslahaan Saat Request Data");
            }
          });
      } else {
        this.setState({
          isLoading : false
        })
        document.body.style.cursor = "initial"
        ToastNotification("info", "Barang Sudah Ada Di Tabel");
      }
    }, 1500);
  }
  hitungTotal(e) {
    let total = 0;
  if(getUserdata().kode_toko === "PTR"){
     total = this.props.harga_atribut + this.props.harga_jual + parseInt(e.target.value || "0")
  }else{
      total = pembulatan(this.props.harga_atribut + this.props.harga_jual + parseInt(e.target.value || "0"))
    }
    this.props.change("total", total);
  }
  onChangeOngkos(e) {
    let total = 0;
    if(getUserdata().kode_toko === "PTR"){
      console.log(parseFloat(this.state.harga_baru));
      total = parseInt(this.props.harga_atribut || 0) + parseFloat(this.state.harga_baru || 0) + parseInt(NumberOnly(e.target.value) ||  0) || 0
    }else{
      total = pembulatan(parseInt(this.props.harga_atribut || 0) + parseInt(this.state.harga_baru || 0) + parseInt(NumberOnly(e.target.value) ||  0) || 0)
    }
    this.props.change("total", total);
  }
  onChangeBerat(e){
    // console.log(getUserdata().kode_toko);
    if(getUserdata().kode_toko === "PTR"){
      let harga = ((parseFloat(this.props.kadar) / 100) * parseFloat(this.props.harga_skrg) ) * parseFloat(e.target.value || 0) || 0;
      let total = this.props.harga_atribut + parseInt(harga) + parseInt(this.props.ongkos || "0")
      this.props.change("total", total);
      this.props.change("harga_jual",harga)
      this.setState({
        harga_baru : harga
      })
      let parameterharga = (parseInt(harga) / parseFloat(e.target.value || 0)) * this.props.kadar / 100 || 0
      this.props.change("harga_pergram",parseInt(Math.ceil(parameterharga) || 0))

    }else{
      let harga = pembulatan(((parseFloat(this.props.kadar) / 100) * parseFloat(this.props.harga_skrg) ) * parseFloat(e.target.value || 0)) || 0;
      let total = pembulatan(this.props.harga_atribut + parseInt(harga) + parseInt(this.props.ongkos || "0"))
      this.props.change("total", total);
      this.props.change("harga_jual",harga)
      this.setState({
        harga_baru : harga
      })
      let parameterharga = (parseInt(harga) / parseFloat(e.target.value || 0)) * this.props.kadar /100 || 0
      this.props.change("harga_pergram",parseInt(Math.ceil(parameterharga) || 0))
    }
   
  }

  onChangeHarga(e){
    let parameterharga = (parseInt(NumberOnly(e.target.value)) / parseFloat(this.props.berat)) * this.props.kadar /100
    
    let harga_jual = 0;
    if (parameterharga === Math.floor(parameterharga)) {
      harga_jual = parseFloat(Math.ceil(parameterharga));
    }else{
      harga_jual = parseFloat(Math.floor(parameterharga));
    }

    this.props.change("harga_pergram",harga_jual || 0)

    this.props.change("harga_jual",NumberOnly(e.target.value))
    let total = 0;
    if(getUserdata().kode_toko === "PTR"){
      total = parseInt(this.props.harga_atribut || 0) + parseInt(NumberOnly(e.target.value) || 0) + parseInt(this.props.ongkos ||  0) || 0
    }else{
      total = pembulatan(parseInt(this.props.harga_atribut || 0) + parseInt(NumberOnly(e.target.value) || 0) + parseInt(this.props.ongkos ||  0) || 0)
    }
    this.props.change("total", total);
    this.setState({
      harga_baru : NumberOnly(e.target.value)
    })
  }

  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  // aktifotoriasi(e) {
  //   this.props.change("username", "");
  //   this.props.change("password", "");

  //   let hasil = e.target.value.split(" ");
  //   let harga_total = hasil[1].replace(/[^0-9]+/g, "");
  //   let harga_jual = this.props.harga_jual_tmp;
  //   if (parseInt(harga_total) < parseInt(harga_jual)) {
  //     localStorage.setItem("otorisasi", "true");
  //   } else {
  //     localStorage.setItem("otorisasi", "false");
  //   }
  // }

  cekOtorisasi(e) {
    let hasil = {
      user_id: e.target.value,
      password: document.getElementById("otorisasi_penjualan_username").value,
    };
    postData2("authorization", hasil)
      .then((resposn) => {
        ToastNotification("success", "Otorisasi Berhasil");
        localStorage.setItem("otorisasi", "false");
        this.setState({
          otorisasi: true,
        });
      })
      .catch((err) => {
        this.setState({
          otorisasi: false,
        });
      });
  }

  componentDidMount() {
    this.props.dispatch(setCamera(false));
    this.props.change("foto", "");
    this.props.dispatch(getDataSales());
    this.props.dispatch(getDataMarketplace());
    this.props.dispatch(getParaSystemId("KATEGORI_HARGA"));
    setTimeout(() => {
      document.getElementById("kode_barcode").focus();
    }, 100);
  }
  render() {
    return (
      <>
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-12 col-md-10">
            <div className="row">
              <div className="col-12 col-md-3">
                <Field
                  onChange={(e) => this.getBarcode(e.target.value)}
                  onBlur={() => this.props.change("total", this.props.total)}
                  name="kode_barcode"
                  label="Kode Barcode"
                  type="text"
                  tabIndex="1"
                  normalize={(value) => value.replace(/[^\d]/g, "")}
                  id="kode_barcode"
                  placeholder="Masukan Kode Barcode"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-5">
                <Field
                  name="foto"
                  type="hidden"
                  readOnly={true}
                  component={HiidenFiled}
                />
                <Field
                  name="nama_barang"
                  label="Nama Barang"
                  type="text"
                  readOnly={true}
                  placeholder="Masukan Nama Barang"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
                <Field
                  name="nama_atribut"
                  label="Nama Atribut"
                  type="text"
                  readOnly={true}
                  placeholder="Masukan Nama Atribut"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
                <Field
                  name="harga_atribut"
                  label="Harga Atribut"
                  type="text"
                  {...currencyMask}
                  onChange={this.hitungTotal}
                  readOnly={true}
                  placeholder="Masukan Harga Atribut"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
                <Field
                  name="berat_atribut"
                  label="Berat Atribut"
                  type="number"
                  readOnly={true}
                  placeholder="Masukan Berat Atribut"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
                <Field
                  tabIndex="2"
                  id="berat"
                  name="berat"
                  label="Berat Jual"
                  type="text"
                  normalize={Gramasi}
                  onChange={(e) => this.onChangeBerat(e)}
                  // readOnly={this.state.berat_jual}
                  placeholder="Masukan Berat Jual"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
                <Field
                  name="harga_jual_tmp"
                  type="hidden"
                  readOnly={true}
                  component={HiidenFiled}
                />
                <Field
                  tabIndex="3"
                  name="harga_jual"
                  label="Harga Jual"
                  type="telp"
                  {...currencyMask}
                  onChange={(e) => this.onChangeHarga(e)}
                  readOnly={this.state.harga_jual}
                  placeholder="Masukan Harga Jual"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
                <Field
                  name="harga_pergram"
                  label="Harga/Gram"
                  type="text"
                  readOnly={true}
                  {...currencyMask}
                  placeholder="Masukan Harga/Gram"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4">
              <Field
                id="marketplace"
                label="Marketplace"
                name="marketplace"
                options={this.props.getDataMarketplace
                  .filter((list) => list.kode_marketplace !== undefined)
                  .map((list) => {
                    let data = {
                      value: list.kode_marketplace,
                      name: list.kode_marketplace + " - " + list.nama_marketplace,
                    };
                    return data;
                  })}
                placeholder={"Silahkan Pilih Marketplace"}
                component={ReanderSelect}
              />
              </div>
              <div className="col-12 col-md-4 d-none">
                <Field
                  name="berat_tmp"
                  label="Berat Tmp"
                  type="telp"
                  readOnly={true}
                  {...currencyMask}
                  placeholder="Masukan Harga Sekarang"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4 d-none">
                <Field
                  name="harga_skrg"
                  label="Harga Sekarang"
                  type="telp"
                  readOnly={true}
                  {...currencyMask}
                  placeholder="Masukan Harga Sekarang"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-4 d-none">
                <Field
                  name="kadar"
                  label="Harga Sekarang"
                  type="telp"
                  readOnly={true}
                  {...currencyMask}
                  placeholder="Masukan Harga Sekarang"
                  component={ReanderField}
                />
              </div>
              <div className="col-12 col-md-2 d-none">
                <Field
                  tabIndex="4"
                  name="premi"
                  label="Premi"
                  type="checkbox"
                  id="switcher_checkbox_1"
                  component={ReanderCheckBox}
                />
              </div>
              <div className="col-12 col-md-6">
              <Field
                  tabIndex="3"
                  name="ongkos"
                  label="Ongkos"
                  type="telp"
                  {...currencyMask}
                  onChange={(e) => this.onChangeOngkos(e)}
                  readOnly={this.state.harga_jual}
                  placeholder="Masukan Harga Jual"
                  component={ReanderField}
                />
                {/* <Field
                  name="ongkos"
                  label="Ongkos"
                  type="telp"
                  tabIndex="5"
                  onChange={(e) => console.log(e)}
                  readOnly={this.state.ongkos}
                  placeholder="Masukan Ongkos"
                  component={ReanderField}
                  {...currencyMask}
                  // normalize={(data) => data && data.toLocaleString('kr-KO')}
                /> */}
              </div>
              <div className="col-12 col-md-6">
                <Field
                  name="total"
                  label="Total"
                  type="telp"
                  readOnly={this.state.total}
                  placeholder="Masukan Total"
                  component={ReanderField}
                  {...currencyMask}
                />
              </div>
            </div>
          </div>
          <div className="col-12 col-md-2">
            <label>Photo</label>
            {/* {console.log(this.props.setCamera === false ? 'benar' : this.props.setCamera === undefined ? 'undefined' : 'salah')} */}
            <div className="card image-upload">
              {this.state.ModalWebCam === true ? (
                <div className="container">{this.loadcamera()}</div>
              ) : (
                <img
                  alt="img"
                  name="foto"
                  src={
                    this.props.setCamera === false || undefined
                      ? imgfound
                      : this.props.setCamera === undefined
                      ? imgfound
                      : this.props.setCamera
                  }
                  height="140px"
                />
              )}
            </div>
          </div>
        </div>

        <div className="row">
          <div
            className={
               "col-4"
            }
          >
            <label> &nbsp;</label>
            {/* {console.log(this.props.total)} */}
            <button
              tabIndex="5"
              className="btn btn-primary btn-block"
              disabled={
                this.state.isLoading
                  ? true
                  : this.props.total === 0 ? true : false
              }
              type="submit"
            >
              {this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>

          <div
            className={
              "col-4"
               
            }
          >
            <label> &nbsp;</label>
            <button
              // tabIndex="5"
              onClick={this.props.hideModal}
              className="btn btn-outline-secondary btn-block"
              disabled={
                this.state.isLoading
                  ? true
                  : false
              }
              type="button"
            >
              {this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Tutup
                </>
              ) : (
                "Tutup"
              )}
            </button>
          </div>
          <div className="col-2">
            <label> &nbsp;</label>
            <div className="input-group">
              <div className="custom-file">
                <input
                  type="file"
                  name="photo"
                  autoComplete="off"
                  className="custom-file-input"
                  id="exampleInputFile"
                  onChange={(e) => this.getValue(e)}
                />
                <label className="custom-file-label" htmlFor="exampleInputFile">
                  Pilih gambar
                </label>
              </div>
            </div>
          </div>
          <div className="col-2">
            <label> &nbsp;</label>
            <button
              className="btn btn-block btn-primary"
              type="button"
              onClick={() => this.showModalWebcam()}
            >
              {" "}
              WebCam{" "}
            </button>
          </div>
        </div>
      </form>
      {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </>
    );
  }
}

// ModalTambahBarang = reduxForm({
//   form: "ModalTambahBarang",
//   enableReinitialize: true,
//   validate: ValidasiMasterDataHadiah,
// })(ModalTambahBarang);
// export default connect(maptostate, null)(ModalTambahBarang);

ModalTambahBarang = reduxForm({
  form: "ModalTambahBarang",
  enableReinitialize: true,
  validate: ValidasiFormPenjualanDataBarang,
})(ModalTambahBarang);

export default connect((state) => {
  const selector = formValueSelector("ModalTambahBarang"); // <-- same as form name
  const { harga_jual, ongkos, berat, harga_jual_tmp, harga_atribut,harga_skrg,kadar } = selector(
    state,
    "harga_jual",
    "ongkos",
    "berat",
    "harga_jual_tmp",
    "harga_atribut",
    "harga_skrg",
    "kadar"
  );
 
  
  // console.log(parseFloat(berat));
  // console.log(harga_jual);
  return {
    // DataSystem: state.utility.getParaSystemId,
    kategori_harga: state.utility.getParaSystemId.value,
    harga_jual_tmp: harga_jual_tmp,
    kadar: kadar,
    harga_skrg: harga_skrg,
    berat: berat,
    harga_atribut: harga_atribut,
    ongkos: ongkos,
    getDataMarketplace: state.datamaster.getDataMarketplace,
    harga_jual: ((parseFloat(kadar) / 100) * parseFloat(harga_skrg) )* parseFloat(berat || 0) || 0,
    total:
      pembulatan(parseFloat(harga_jual || 0) +
      parseFloat(ongkos || 0) +
      parseFloat(harga_atribut || 0) || 0),
    // harga_pergram: parameterharga,
    DataSales: state.datamaster.getDataSales,
    setCamera: state.databarang.setCamera,
    
  };
})(ModalTambahBarang);
