import {
    React,
    reduxForm, Field,
    ReanderField, ReanderSelect,
    Component,
    connect,

    NumberOnly,
    Tabel,
    formValueSelector,
    createNumberMask,
    ToastNotification,
    Gramasi,
  } from "../../../components/helpers/library";
  
  import ValidasiPenjualan from "../../../Validasi/Penjualan/ValidasiPenjualan";
  import { getDataJenis } from "../../../actions/datamaster_action";
import { getData } from "../../../config/axios";
import { Kadar } from "../../../Validasi/normalize";
import { pembulatan } from "../../../components/helpers/function";

  const currencyMask = createNumberMask({
    prefix: "Rp. ",
    locale: "kr-KO",
  });
  class ModalTukarBarang extends Component {
    constructor(props) {
      super(props);
      this.state = {
        showMember: true,
      };
    }

    hitungTotal(){
      this.props.change("harga_per_gram", pembulatan(this.props.hasil));
    }
    componentDidMount(){
      this.props.dispatch(getDataJenis())
      setTimeout(() => {
        document.getElementById("kode_faktur").focus()
      }, 100);
    }
    cariFaktur(e) {
      // console.log(e.target.value);
      if (e.target.value === "" || e.target.value === undefined) {
        
      }else{
        clearTimeout(this.delay);
        this.delay = setTimeout(() => {
          getData("penjualan/get/by-no-jual/" + e.target.value).then((res) => {
            this.props.change("nama_barang", res.data.nama_barang)
            this.props.change("Berat", res.data.berat)
            this.props.change("harga_nota", parseInt(res.data.harga_jual) + parseInt(res.data.harga_atribut) )
            this.props.change("harga_per_gram", pembulatan(res.data.harga_gram))
            this.props.change("kadar", res.data.kadar)
            this.props.change("jenis", res.data.kode_dept + " - " + res.data.nama_dept)
          }).catch((err) => {
            ToastNotification('info', err.response?.data)
          })
        }, 2000);
      }
    }
    render() {
      return (
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <div className="row">
          <div className="col-3">
              <Field
                id="kode_faktur"
                name="no_faktur"
                component={ReanderField}
                onChange={(e) => this.cariFaktur(e)}
                type="text"
                label="Kode Faktur"
                placeholder="Masukan Kode Faktur"
                normalize={(value)=> value && value.toUpperCase()}
              />
            </div>
            <div className="col-3">
              <Field
                id="jenis"
                name="jenis"
                label="Kode Jenis"
                placeholder="Silahkan Pilih Kode Jenis"
                onChange={(data) => this.props.change("nama_barang", data.split("-")[1])}
                options={this.props.dataJenis.map((data) => {
                    return {
                        value : data.kode_dept + " - " + data.nama_dept,
                        name : data.kode_dept + " - " + data.nama_dept
                    }
                })}
                component={ReanderSelect}
              />
            </div>
            <div className="col-3">
              <Field
                id="nama_barang"
                name="nama_barang"
                component={ReanderField}
                type="text"
                label="Nama Barang"
                placeholder="Masukan Nama Barang"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-3">
              <Field
                id="kadar"
                name="kadar"
                component={ReanderField}
                type="text"
                label="Kadar"
                placeholder="Masukan Kadar"
                normalize={Kadar}
              />
            </div>
            <div className="col-3">
              <Field
                id="berat"
                name="Berat"
                component={ReanderField}
                type="text"
                label="Berat"
                onChange={this.hitungTotal()}
                placeholder="Masukan Berat"
                normalize={Gramasi}
              />
            </div>
            <div className="col-3">
              <Field
                name="harga_nota"
                component={ReanderField}
                type="text"
                normalize={NumberOnly}
                label="Harga Nota"
              readOnly={true}
                {...currencyMask}
                placeholder="Masukan Harga Nota"
              />
            </div>
            <div className="col-3">
              <Field
                name="harga_beli"
                component={ReanderField}
                type="text"
                onChange={this.hitungTotal()}
                label="Harga Beli"
                placeholder="Masukan Harga Beli"
                {...currencyMask}
              />
            </div>
            <div className="col-3">
              <Field
                name="harga_per_gram"
                component={ReanderField}
                type="text"
                normalize={NumberOnly}
                label="Harga/Gram"
                {...currencyMask}
                placeholder="Masukan Harga/Gram"
              />
            </div>
          </div>
  
          <div className="row">
            <div className="col-8"></div>
            <div className="col-4">
              <button
                className="btn btn-primary btn-block"
                disabled={this.props.isLoadingDataCustomer || this.props.harga_beli === undefined || "" || this.props.harga_beli <= 0}
                type="submit"
              >
                {this.props.isLoadingDataCustomer ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Menyimpan
                  </>
                ) : (
                  "Simpan Barang"
                )}
              </button>
            </div>
            <div className="col-lg-12">
            <Tabel
                // handleClick={() => this.simpanpembbayaran()}
                keyField="nama_barang"
                // tambahData={true}
                // btnText="Tambah Pembyaran"
                exportCsv={false}
                data={this.props.barang || []}
                columns={this.props.columns}
                empty={true}
                pagination="off"
                textEmpty="Data Barang Kosong"
            />
          </div>
          </div>
        
        </form>
      );
    }
  }
  
  ModalTukarBarang = reduxForm({
    form: "ModalTukarBarang",
    enableReinitialize: true,
    validate: ValidasiPenjualan,
  })(ModalTukarBarang);
  const selector = formValueSelector('ModalTukarBarang') // <-- same as form name

  export default connect((state) => {
    // console.log(parseFloat(selector(state, 'harga_beli')) / parseFloat(selector(state, 'Berat')) || 0);
    return {
        DataSales: state.datamaster.getDataSales,
        dataJenis : state.datamaster.getDataJenis,
        berat: selector(state, 'Berat'),
        harga_beli : selector(state, 'harga_beli'),
        hasil : parseFloat(selector(state, 'harga_beli')) / parseFloat(selector(state, 'Berat')) || 0
      };
  })(ModalTukarBarang);
  