const maptostate = (state) => {
  return {
    setBarangPenjualan: state.penjualan.setBarangPenjualan,
    DataSales: state.datamaster.getDataSales,
    hideModal: state.datamaster.modalDialog,
    setBarangTukar: state.penjualan.setBarangTukar
  };
};

const hapusDataBaki =  (params,props) => {
  Swal.fire({
    title: "Anda yakin ingin mengapus barang ini ? ",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      let cek =
      localStoragedecryp("data_tukar_barang_temp").length === 0
        ? []
        : JSON.parse(localStoragedecryp("data_tukar_barang_temp"));
      let hasil_cek = cek.detail_barang.findIndex(
        (hasil) => hasil.nama_barang === params
      );
      cek.detail_barang.splice(hasil_cek, 1)
      setItem("data_tukar_barang_temp", cek)
      props.dispatch(setBarangTukar(cek));
  }
  }).catch((err) => {
    ToastNotification("error", err.response?.data)
  })
};

