const ValidasiFormPenjualanDataBarang = (value) => {
    const errors = {};
    if (!value.kode_barcode) {
        errors.kode_barcode = "Kode Barcode Tidak Boleh Kosong";
    }
    if (!value.nama_customer) {
        errors.nama_customer = "Nama Customer Tidak Boleh Kosong";
    }
    if (!value.no_hp) {
        errors.no_hp = "No HP Tidak Boleh Kosong";
    }
    // if (!value.premi) {
    //     errors.premi = "Premi Tidak Boleh Kosong";
    // }
    if (!value.alamat_customer) {
        errors.alamat_customer = "Alamat Customer Tidak Boleh Kosong";
    }

    return errors;
}

export default ValidasiFormPenjualanDataBarang;


