import {
  React,
  Component,
  connect,
  Field,
  createNumberMask,
  formValueSelector,
  reduxForm,
  NumberOnly,
  localStoragedecryp,
  ReanderField,
  ReanderSelect,
  getItem,
  setItem,
} from "../../../components/helpers/library";
import { getDataSales } from "../../../actions/datamaster_action";
import ValidasiPembayaran from "../../../Validasi/Penjualan/ValidasiPembayaran";
import { getData2 } from "../../../config/axios";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
const jenis_pembayaran = [
  {
    value: "CASH",
    name: "CASH",
  },
  {
    value: "DEBET",
    name: "DEBET",
  },
  {
    value: "TRANSFER",
    name: "TRANSFER",
  },
  {
    value: "CREDIT",
    name: "CREDIT",
  },
];
class HeadInputPembayaran extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      type_pembayaran: "CASH",
      tampilPont: false,
      columns: [
        // {
        //   dataField: "nama_bank",
        //   text: "Nama Bank",
        //   footerAttrs: {
        //     colSpan: "2",
        //   },
        //   footer: "Grand Total",
        // },
        {
          dataField: "type",
          text: "Type",
          footerAttrs: {
            colSpan: "1",
          },
          footer: "Grand Total",
        },
        {
          dataField: "Total",
          text: "Total",
          footer: "",
        },
      ],
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    let data_barang_penjualan = JSON.parse(
      localStoragedecryp("data_barang_penjualan_tmp")
    );

    setTimeout(() => {
      let total_bayar = NumberOnly(
        document.getElementById("grand_total_pembayaran").value
      );
      let total_dp = NumberOnly(document.getElementById("total_dp").value);

      if (parseInt(total_dp) >= parseInt(total_bayar)) {
        this.props.change("harus_bayar_rp", parseInt(0));
        let hasil = parseInt(total_bayar) - parseInt(total_dp);
        this.props.change(
          "kembali",
          hasil.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        );
      } else {
        this.props.change(
          "harus_bayar_rp",
          parseInt(total_bayar) - parseInt(total_dp)
        );
      }
    }, 100);

    let total_dp = getItem("total_dp");
    this.props.change("total_dp", total_dp);
    let total = 0;
    data_barang_penjualan.forEach((item, index) => {
      // console.log(item.total);
      total = total + item.total;
      this.props.change("grand_total_pembayaran", total);
      this.props.change("harus_bayar_rp", total);
    });
    this.props.change("jenis", "CASH");
    setTimeout(() => {
      document.getElementById("cash_trx_penjualan").focus();
    }, 100);
  }
  hitungTotal() {
    // console.log(this.props.nominal_kembali);
    this.props.change("kembali", this.props.kembali);
    this.props.change("nominal_kembali", this.props.nominal_kembali);
  }

  pilihPembyaran(e) {
    if (e === "CASH") {
      this.setState({
        type_pembayaran: "CASH",
      });
    } else if (e === "TRANSFER") {
      this.setState({
        type_pembayaran: "TRANSFER",
      });
    } else if (e === "DEBET") {
      this.setState({
        type_pembayaran: "DEBET",
      });
    } else {
      this.setState({
        type_pembayaran: "CREDIT",
      });
    }
    this.props.change("cash", "");
  }
  copyValue(e) {
    this.props.change("cash", parseInt(NumberOnly(e)));
  }
  getPoin(e) {
    setItem("poin_penjualan_member", parseInt(e) || 0);
    getData2("para-system/key/CASHBACK_POIN").then((res) => {
      // console.log(res.data.value);
      let hasil = parseInt(e || 0) * parseInt(res.data.value || 0);
      this.props.change("konversi_poin", hasil);
    });
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              id="grand_total_pembayaran"
              name="grand_total_pembayaran"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              readOnly={true}
              {...currencyMask}
              label="Total Harga Jual"
              placeholder="Total Harga Jual"
            />
          </div>
          <div className="col-4">
            <Field
              id="total_dp"
              name="total_dp"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              readOnly={true}
              {...currencyMask}
              label="Total Dp"
              placeholder="Total Dp"
            />
          </div>
          <div className="col-4">
            <Field
              id="harus_bayar_rp"
              name="harus_bayar_rp"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              readOnly={true}
              {...currencyMask}
              label="Harus Bayar Rp"
              placeholder="Harus Bayar Rp"
            />
          </div>
          <div className="col-6">
            <Field
              name="jenis"
              label="Pilih Jenis Bayar"
              placeholder="Pilih Jenis Pembayaran"
              options={jenis_pembayaran}
              onChange={(e) => this.pilihPembyaran(e)}
              component={ReanderSelect}
            />
          </div>

          <div
            className="col-6"
            style={{
              display:
                this.state.type_pembayaran === "CASH"
                  ? "none"
                  : this.state.type_pembayaran === "DEBET"
                  ? "block"
                  : "block",
            }}
          >
            <Field
              tabIndex="1"
              name="nama_bank"
              id="nama_bank"
              component={ReanderField}
              type="text"
              label="Bank"
              placeholder="Masukan Nama Bank"
            />
          </div>
          <div
            className="col-6"
            style={{
              display:
                this.state.type_pembayaran === "CASH"
                  ? "none"
                  : this.state.type_pembayaran === "DEBET"
                  ? "none"
                  : "block",
            }}
          >
            <Field
              tabIndex="1"
              name="no_card"
              id="no_card"
              component={ReanderField}
              type="text"
              normalize={NumberOnly}
              label={
                this.state.type_pembayaran === "TRANSFER" ? "No Rek" : "Card No"
              }
              placeholder={
                this.state.type_pembayaran === "TRANSFER"
                  ? "Masukan No Rek"
                  : "Masukan Card No"
              }
            />
          </div>
          <div className="col-6">
            <Field
              tabIndex="1"
              name="cash"
              id="cash_trx_penjualan"
              component={ReanderField}
              type="text"
              label="Nominal"
              onChange={this.hitungTotal()}
              {...currencyMask}
              normalize={NumberOnly}
              placeholder="Masukan Nominal"
            />
          </div>
          <div
            className="col-6"
            style={{
              display:
                this.state.type_pembayaran === "CREDIT" ? "block" : "none",
            }}
          >
            <Field
              tabIndex="1"
              name="fee"
              id="fee"
              component={ReanderField}
              type="text"
              label="Fee (%)"
              normalize={NumberOnly}
              placeholder="5"
            />
          </div>
          <div className="col-6 d-none">
            <Field
              tabIndex="1"
              id="nominal_kembali"
              name="nominal_kembali"
              component={ReanderField}
              type="text"
              label="Nominal"
              placeholder="Masukan Nominal"
            />
          </div>
          <div className="col-6">
            <Field
              name="kembali"
              id="sisa_bayar"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Sisa"
              onFocus={(e) => this.copyValue(e.target.value)}
              //   {...currencyMask}
              // normalize={NumberOnly}
              placeholder="Sisa Pembayaran"
            />
          </div>
          <div
            className={
              this.state.type_pembayaran === "CASH"
                ? "col-6 text-right"
                : this.state.type_pembayaran === "TRANSFER"
                ? "col-6 text-right"
                : "col-12 text-right"
            }
          >
            <br />
            <label>&nbsp;</label>
            <button
              tabIndex="2"
              className="btn btn-primary"
              disabled={!this.props.lebih}
            >
              {" "}
              <i className="fa fa-plus"></i>
            </button>
          </div>
          <div className="col-12 text-right">
            {getItem("data_customer_tmp").jenis_pelanggan === "MEMBER" ? (
              <>
                {this.state.tampilPont === false ? (
                  <>
                    <button
                      type="button"
                      onClick={() => {
                        this.setState({ tampilPont: true });
                        setTimeout(() => {
                          document.getElementById("poin").focus();
                        }, 100);
                      }}
                      className="btn btn-primary"
                    >
                      Cashback Poin &nbsp; <i className="fa fa-plus"></i>
                    </button>
                  </>
                ) : (
                  <>
                    <button
                      type="button"
                      onClick={() => {
                        this.setState({ tampilPont: false });
                        this.props.change("poin", "");
                        this.props.change("konversi_poin", "");
                      }}
                      className="btn btn-primary"
                    >
                      Cashback Poin &nbsp; <i className="fa fa-plus"></i>
                    </button>
                  </>
                )}
              </>
            ) : null}
          </div>
          {this.state.tampilPont === true ? (
            <>
              <div className="col-12 text-right">&nbsp;</div>
              <div className="col-6">
                <Field
                  name="poin"
                  id="poin"
                  component={ReanderField}
                  type="text"
                  label="Poin"
                  onChange={(e) => this.getPoin(e.target.value)}
                  //   {...currencyMask}
                  normalize={NumberOnly}
                  placeholder="Masukan Poin"
                />
              </div>
              <div className="col-6">
                <Field
                  name="konversi_poin"
                  component={ReanderField}
                  type="text"
                  label="Konversi Poin Ke Rupiah"
                  {...currencyMask}
                  readOnly={true}
                  // normalize={NumberOnly}
                  placeholder="Konversi Poin Ke Rupiah"
                />
              </div>
            </>
          ) : null}
        </div>
      </form>
    );
  }
}

HeadInputPembayaran = reduxForm({
  form: "HeadInputPembayaran",
  enableReinitialize: true,
  validate: ValidasiPembayaran,
})(HeadInputPembayaran);

const selector = formValueSelector("HeadInputPembayaran"); // <-- same as form name

export default connect((state) => {
  const { cash, harus_bayar_rp } = selector(state, "harus_bayar_rp", "cash");
  let data = JSON.parse(localStorage.getItem("trx_pemabayran_penjualan")) || [];
  let total = 0;
  data.forEach((element) => {
    total = element.nominal + total;
  });
  let hasil = parseFloat(total || 0) - parseFloat(harus_bayar_rp || 0);
  let status = false;

  if (parseInt(harus_bayar_rp) >= parseInt(hasil)) {
    status = true;
  } else {
    status = false;
  }
  // console.log("Hasil" , hasil);
  return {
    status: status,
    kembali: hasil.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    nominal_cash: cash,
    nominal_kembali: hasil + parseInt(cash),
    lebih: hasil < 0,
  };
})(HeadInputPembayaran);
