import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
// import { ReanderSelect } from "../../../components/helpers/field";

const maptostate = (state) => {
  return {
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
  };
};

class AmbilPesanan extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    setTimeout(() => {
      document.getElementById("nomor_pesanan").focus();
    }, 100);
  }
  render() {
    return (
      <>
        <form onSubmit={this.props.handleSubmit} autoComplete="off">
          <label htmlFor="">Masukan Nomor Pesanan</label>
          <div className="input-group mb-3">
            <input
              type="text"
              id="nomor_pesanan"
              className="form-control"
              placeholder="Nomor Pesanan"
              aria-label="Nomor Pesanan"
              aria-describedby="basic-addon2"
            />
            <div className="input-group-append">
              <button
                className="btn btn-primary"
                disabled={this.props.isLoadingPesanan}
                onClick={() =>
                  this.props.getNomorPesanan(
                    document.getElementById("nomor_pesanan")
                  )
                }
                type="button"
              >
                {this.props.isLoadingPesanan ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Pesanan"
                )}
              </button>
              {/* <button
                className="btn btn-primary"
                type="button"
                onClick={() =>
                  this.props.getNomorPesanan(
                    document.getElementById("nomor_pesanan")
                  )
                }
              >
                Search
              </button> */}
            </div>
          </div>
          {/* <div>
          <Field
              id="list_barang"
              name="list_barang"
              label="List Barang"
              placeholder="Silahkan List Barang"
              options={[]}
              component={ReanderSelect}
            />
          </div>
          <div className="mt-2">
            <button type="submit" className="btn btn-primary">Simpan</button>
          </div> */}
        </form>
      </>
    );
  }
}

AmbilPesanan = reduxForm({
  form: "AmbilPesanan",
  enableReinitialize: true,
})(AmbilPesanan);
export default connect(maptostate, null)(AmbilPesanan);
