import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { hideModal } from "../../../../actions/datamaster_action";
import { setBarangPenjualan } from "../../../../actions/penjualan_action";
import {
  localStorageencryp,
  localStoragedecryp,
  setItem,
  getItem,
} from "../../../../components/helpers/function";
import { ToastNotification } from "../../../../components/helpers/notification";
import Tabel from "../../../../components/helpers/tabel";
import { getData2 } from "../../../../config/axios";
import FilterTitipan from "./FilterTitipan";

class AmbilTitipan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      type_pembayaran: "CASH",
      databarang: [],
      columns: [
        {
          dataField: "_id",
          text: "No Titip",
          footerAttrs: {
            colSpan: "3",
          },
          footer: "Total",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },
        {
          dataField: "no_hp",
          text: "No Hp",
        },

        {
          dataField: "total_dp",
          text: "Total Dp",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "total_transaksi",
          text: "Total Transaksi",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.pilihdata(row)}
                    className="btn btn-danger mr-3"
                  >
                    Pilih Barang
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
  }

  pilihdata(data) {
    // console.log(data);
    // return false
    let customer = getItem("data_customer_tmp");
    if (customer.length !== 0) {
      if (customer.no_hp !== data.no_hp) {
        ToastNotification("info", "Customer Tidak Boleh Berbeda");
        return false;
      }
    }
    let cek =
      localStoragedecryp("data_barang_penjualan_tmp").length === 0
        ? []
        : JSON.parse(localStoragedecryp("data_barang_penjualan_tmp"));
    let hasil_cek = cek.findIndex(
      (hasil) => hasil.kode_barcode === data.kode_barcode
    );
    if (hasil_cek === -1) {
      data.detail_barang.forEach(element => {
          let row = {
            no_titip_group: element.no_titip_group || "-",
            total_dp: element.total_dp || 0,
            kode_barcode: element.kode_barcode || 0,
            nama_barang: element.nama_barang || "-",
            nama_atribut: element.nama_atribut || "-",
            berat_jual: parseFloat(element.berat) || 0,
            berat_atribut: parseFloat(element.berat_atribut) || 0,
            harga_atribut: parseInt(element.harga_atribut) || 0,
            harga_jual: parseInt(element.harga_jual) || 0,
            harga_pergram: parseInt(element.harga_pergram) || 0,
            ongkos: parseInt(element.ongkos) || 0,
            total: parseInt(element.harga_total) || 0,
            status_premi: element.premi ? "PREMI" : "NONPREMI",
            pembayaran: element.pembayaran,
          };
        cek.push(row);
        
          let data_customer = {
            alamat_customer: element.alamat_customer,
            jenis_pelanggan: element.kode_customer === "NONMEMBER" ? "NONMEMBER" : element.kode_customer,
            kode_customer: element.kode_customer,
            kode_sales: element.kode_sales,
            nama_customer: element.nama_customer,
            no_hp: element.no_hp,
        };
        
        let total_dp = 0;
          element.pembayaran.forEach((element) => {
            total_dp = total_dp + element.jumlah_rp;
          });
          setItem("total_dp", total_dp);
      localStorageencryp("data_customer_tmp", JSON.stringify(data_customer));
      });
        
      localStorageencryp("data_barang_penjualan_tmp", JSON.stringify(cek));
      this.props.dispatch(setBarangPenjualan(cek));
      
      
      

      // let cek_pembayaran = localStoragedecryp("trx_pemabayran_penjualan").length === 0
      //     ? []
      //     : JSON.parse(localStoragedecryp("trx_pemabayran_penjualan"));
      // // let total_dp = 0;
      // data.pembayaran.forEach((element) => {
      //   let hasil_cek_pembaran = cek_pembayaran.findIndex(
      //     (hasil) => hasil.jenis === element.jenis
      //   );
      //   if (hasil_cek_pembaran === -1) {
      //     let row = {
      //       jenis: element.jenis,
      //       keterangan: "TITIPAN",
      //       nominal: element.jumlah_rp,
      //     };
      //     cek_pembayaran.push(row);
      //     setItem("trx_pemabayran_penjualan", cek_pembayaran);
      //   } else {
      //     let data_pembayaran = getItem("trx_pemabayran_penjualan");
      //     data_pembayaran = data_pembayaran.map((element2) => {
      //       if (element2.jenis === element.jenis) {
      //         element2.nominal += element.jumlah_rp;
      //       }
      //       return element2;
      //     });
      //     // console.log(data_pembayaran);
      //     setItem("trx_pemabayran_penjualan", data_pembayaran);
      //   }
      // });
  
      // let data_pembayaran = [];
      // let total_dp = 0;
      // data.pembayaran.forEach((element) => {
      //   let row = {
      //     jenis: element.jenis,
      //     keterangan: "TITIPAN",
      //     nominal: element.jumlah_rp,
      //   };
      //   total_dp = total_dp + element.jumlah_rp;
      //   data_pembayaran.push(row);
      // });
      // let tot_dp = getItem("trx_pemabayran_penjualan");
      // let total_dp = 0;
      // tot_dp.forEach((element) => {
      //   total_dp = total_dp + element.nominal;
      // });
      // setItem("total_dp", total_dp);

    } else {
      ToastNotification("info", "Barang Sudah Ada Di Tabel");
      return false
    }

    this.props.dispatch(hideModal());
  }
  filterPencarian(data) {
    this.setState({
      isLoading: true,
    });
    getData2("titip/valid/" + data.filter + "/" + data.pencarian)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
        }

        this.setState({
          databarang: res.data,
          isLoading: false,
        });

        let data_filter = {
          filter: data.filter,
          pencarian: data.pencarian,
          data_barang: res.data,
        };
        setItem("filter_titipan", data_filter);
      })
      .catch((err) => {
        this.setState({
          isLoading: false,
        });
        ToastNotification(
          "info",
          err.response === undefined  ? "Terjadi Kesalahan Saat Request Data" : err.response.data.message
        );
      });
  }
  render() {
    return (
      <>
        <FilterTitipan
          isLoading={this.state.isLoading}
          onSubmit={(data) => this.filterPencarian(data)}
        />
        <Tabel
          // handleClick={() => this.simpanpembbayaran()}
          keyField="kode_barcode"
          // tambahData={true}
          // btnText="Tambah Pembyaran"
          exportCsv={false}
          data={
            this.state.databarang.length === 0
              ? getItem("filter_titipan").data_barang || []
              : this.state.databarang
          }
          columns={this.state.columns}
          empty={true}
          textEmpty="Data Barang Kosong"
        />
      </>
    );
  }
}

AmbilTitipan = reduxForm({
  form: "AmbilTitipan",
  enableReinitialize: true,
})(AmbilTitipan);

export default connect()(AmbilTitipan);
